$redcolor: #901c1c;
$orangecolor: #d3573c;
$bordercolor: #c4c4c4;
$pricecolor: #757575;
$font: calibri;

* {
  font-family: $font;
}

a {
  text-decoration: none;
}

body {
  margin: 0 auto;
  scroll-behavior: smooth;
}

header {
  display: flex;
  float: left;
  width: 70%;
  padding-top: 0.3em;
}

.logo {
  width: 17em;
  margin-right: 4em;
}

nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.btn_select {
  border: 0.5px solid $bordercolor;
  box-sizing: border-box;
  border-radius: 11px;
  background-color: white;
  color: $redcolor;
  margin-left: 0.5em;
  cursor: pointer;
  font-size: 0.9em;
  text-transform: capitalize;
  &:hover,
  &:focus {
    background-color: $redcolor;
    color: white;
  }
}

h1 {
  color: $redcolor;
  width: 20%;
  margin-left: 74%;
  padding-top: 25px;
}

section {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  flex-wrap: wrap;
}

article {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 6em;
}

.photo_graphers {
  height: 17em;
  width: 17em;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.format_photos {
  width: 26em;
}

.photographers_name {
  color: $orangecolor;
  font-size: 3em;
  margin-top: 0.5em;
  margin-bottom: 0.2em;
  text-align: center;
}

.city {
  color: $redcolor;
}

.prices {
  color: $pricecolor;
  padding-bottom: 0.4em;
}

.city,
.slogan,
.prices {
  margin: 0.2em;
  font-size: 1.1em;
}

footer {
  height: 10em;
}

.block-url-ancre {
  width: 100%;
  height: 0px;
  position: absolute;
}

.lien-ancre-tags {
  top: 0;
  padding: 5px;
  background-color: $orangecolor;
  border-radius: 5px;
  position: fixed;
  z-index: 100;
  text-decoration: none;
  color: black;
  font-weight: bold;
  margin-top: 4px;
  text-align: center;
  width: 130px;
  margin-left: 45%;
  margin-right: 40%;
  cursor: pointer;
}

/*Query Smartphone*/
@media screen and (max-width: 840px) {
  body {
    width: 100%;
    margin-left: 10px;
    margin-right: 10px;
  }

  header {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: absolute;
  }

  .logo {
    width: 150px;
    height: 38px;
  }

  h1 {
    font-size: 14px;
    width: 40%;
    height: 40px;
    margin-left: 65%;
    position: relative;
    bottom: 10px;
  }

  main {
    width: 100%;
    position: relative;
  }

  .categories {
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 2em;
    width: 80%;
    justify-content: center;
    margin: 0 auto;
  }

  .btn_select {
    margin-bottom: 0.7em;
    font-size: 12px;
    border: none;
    font-weight: bold;
  }

  section {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding-top: 4em;
  }

  article {
    width: 100%;
    margin-top: 3em;
  }

  .card a {
    height: 270px;
  }

  .photo_graphers {
    height: 13em;
    width: 13em;
  }

  .photographers_name {
    font-size: 36px;
    font-weight: normal;
  }

  .city {
    font-size: 13px;
  }

  .slogan {
    font-size: 10px;
  }

  .prices {
    font-size: 9px;
  }

  .block-url-ancre {
    display: none;
  }
}
